import React from 'react';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography,
  Container,
  Box,
  Link,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

// Example FAQ data array
const faqs = [
  {
    question: 'Are you a broker?',
    answer: (
        <>
          No. Our users connect their brokerage accounts to our platform and trades directly on our platform. All trades are still executed at their broker.

        </>
    ),
  },
  // {
  //   question: 'What exactly do you cover?',
  //   answer: (
  //       <>
  //         We reimburse 50% of trading losses incurred from the first seven trades in stocks and stock ETFs during regular market hours. The limit of coverage is determined by the level of coverage purchased.
  //       </>
  //   ),
  // },
  {
    question: 'How does trade loss insurance work?',
    answer: (
        <>
          Simply purchase coverage before you start trading for the day and you will get reimbursed for losses incurred. We cover 50% of your losses up to your selected coverage limit.
        </>
    ),
  },
  {
    question: 'Which trades are eligible for coverage?',
    answer: (
        <>
          Coverage applies to the first seven(7) trades of the day in stocks, and ETFs during regular market hours(9:30 AM - 4:00 PM EST). Pre-market and after-hour trading are not covered.

        </>
    ),
  },
  {
    question: 'How do I receive my reimbursement?',
    answer: (
        <>
          Losses are calculated after market close(4:00pm EST), and reimbursement are deposited into your Ancile account before the start of the next trading day. You can withdraw funds to your bank account at anytime..
        </>
    ),
  },
  {
    question: 'Do you provide cover for option and cryptocurrency trades?',
    answer: (
        <>
          At this time, coverage is only available for trades in stocks and stock ETFs. We are actively working to extend coverage to other asset classes.

        </>
    ),
  },
];

export default function FAQ() {
  const [expanded, setExpanded] = React.useState<string | false>(false);

  const handleChange =
      (panel: string) => (event: React.SyntheticEvent, isExpanded: boolean) => {
        setExpanded(isExpanded ? panel : false);
      };

  return (
      <Container
          id="faq"
          sx={{
            pt: { xs: 4, sm: 12 },
            pb: { xs: 8, sm: 16 },
            position: 'relative',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            gap: { xs: 3, sm: 6 },
          }}
      >
        <Typography
            component="h2"
            variant="h4"
            sx={{
              color: 'text.primary',
              width: { sm: '100%', md: '60%' },
              textAlign: { sm: 'left', md: 'center' },
            }}
        >
          Frequently asked questions
        </Typography>
        <Box sx={{ width: '100%' }}>
          {faqs.map((faq, index) => (
              <Accordion
                  key={index}
                  expanded={expanded === `panel${index}`}
                  onChange={handleChange(`panel${index}`)}
              >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls={`panel${index}d-content`}
                    id={`panel${index}d-header`}
                >
                  <Typography component="h3" variant="subtitle2">
                    {faq.question}
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Typography
                      variant="body2"
                      gutterBottom
                      sx={{ maxWidth: { sm: '100%', md: '70%' } }}
                  >
                    {faq.answer}
                  </Typography>
                </AccordionDetails>
              </Accordion>
          ))}
        </Box>
      </Container>
  );
}
